import {
  createContext,
  ReactElement,
  ReactNode,
  useEffect,
  useState,
} from "react";

interface AuthContextState {
  loggedIn?: boolean;
  user?: string;
  setUser: (user: string | undefined) => void;
  setLogin: (login: any | undefined) => void;
  setLoggedIn: (loggedIn?: boolean) => void;
}

export const AuthContext = createContext<AuthContextState>({ setLogin: () => {}, setLoggedIn: () => {}, setUser: () => {} });

export function AuthContextProvider(props: {
  children: ReactNode;
}): ReactElement {
  const [loggedIn, setLoggedIn] = useState<boolean | undefined>(undefined);
  const [user, setUser] = useState<string | undefined>(undefined);

  const [login, setLogin] = useState(JSON.parse(localStorage.getItem("user") ?? "{}"));

  useEffect(() => {
    if (login?.data?.email) {
      setLoggedIn(true);
      setUser(login.data.email);
    }
  }, [login, setLoggedIn, setUser]);

  return (
    <AuthContext.Provider value={{ loggedIn, user, setUser, setLogin, setLoggedIn }}>
      {props.children}
    </AuthContext.Provider>
  );
}
