import {ReactElement, useCallback, useContext} from "react";
import {Button} from "@mui/material";
import {BASE_API_URL} from "../util/Constants";
import {Link} from "react-router-dom";
import {AuthContext} from "../AuthContext";

const LOGIN_URL = `${BASE_API_URL}/oauth2/authorization/google?redirect_uri=${process.env.REACT_APP_URL}`;

export function Login(): ReactElement {
  const {setLogin, setUser, setLoggedIn} = useContext(AuthContext);

  const logout = useCallback(() => {
    setLogin({});
    setUser?.(undefined);
    setLoggedIn(undefined);
  }, [setLogin, setUser, setLoggedIn]);

  return (
    <div>
      <Button component={Link} to={LOGIN_URL}>
        Login
      </Button>
      <Button
        onClick={() => {
          localStorage.removeItem("user");
          logout();
        }}
      >
        Logout
      </Button>
    </div>
  );
}
