import {useContext, useEffect} from "react";
import { useLocation, useNavigate } from "react-router-dom";
import {AuthContext} from "../AuthContext";

export function OAuth2Redirect() {
  const {setLogin, setLoggedIn, setUser} = useContext(AuthContext);
  const location = useLocation();
  const navigate = useNavigate();

  useEffect(() => {
    const accessToken = new URLSearchParams(location.search).get("token");
    if (accessToken) {
      const data = parseJwt(accessToken);
      localStorage.setItem("user", JSON.stringify({ data, accessToken }));
      setLogin({ data, accessToken })
      setLoggedIn(true);
      setUser(data.email);
      navigate("/");
    }
  }, [location.search, navigate, setLogin, setLoggedIn, setUser]);
  return null;
}

export function parseJwt(token: string) {
  if (!token) {
    return;
  }
  const base64Url = token.split(".")[1];
  const base64 = base64Url.replace("-", "+").replace("_", "/");
  return JSON.parse(window.atob(base64));
}
