import React, { ReactElement, useEffect } from "react";
import {
  Button,
  Container,
  createTheme,
  CssBaseline,
  Divider,
  Paper,
  TextField,
  ThemeProvider,
  Typography,
} from "@mui/material";
import { useNavigate, useParams } from "react-router-dom";
import { Property, usePropertyFetch } from "../hooks/PropertyFetch";
import { useForm, Controller, Control } from "react-hook-form";
import { BASE_API_URL } from "../util/Constants";

const defaultTheme = createTheme();

interface FormInput {
  description: string;
  address: string;
  city: string;
  state: string;
  zip: string;
}

const defaultValues: FormInput = {
  description: "",
  address: "",
  city: "",
  state: "",
  zip: "",
};

function FormInputText(props: {
  name: keyof FormInput;
  control: Control<FormInput>;
  label: string;
}): ReactElement {
  const { name, control, label } = props;
  return (
    <Controller
      control={control}
      name={name}
      render={({
        field: { onChange, value },
        fieldState: { error },
        formState,
      }) => (
        <TextField
          label={label}
          value={value}
          error={!!error}
          onChange={onChange}
        />
      )}
    />
  );
}

function propertyToForm(input?: Property): FormInput {
  if (input) {
    return {
      ...defaultValues,
      description: input.name,
      ...input.address,
    };
  }
  return defaultValues;
}

function formToProperty(input: FormInput): Property {
  const { address, city, state, zip } = input;
  return {
    name: input.description,
    address: { address, city, state, zip },
  };
}

export default function PropertyEditForm() {
  const { propertyId } = useParams();
  const navigate = useNavigate();

  const { property } = usePropertyFetch(
    `${BASE_API_URL}/properties`,
    propertyId,
  );
  const { control, handleSubmit, reset } = useForm<FormInput>({
    defaultValues: propertyToForm(property),
  });
  const address = property?.address;

  const onSubmit = handleSubmit(async (data) => {
    const body = JSON.stringify(formToProperty(data));
    try {
      const response = await fetch(`${BASE_API_URL}/properties/${propertyId}`, {
        method: "PUT",
        body: body,
        headers: { "Content-Type": "application/json" },
      });
      if (response.ok) {
        navigate(`/properties/${propertyId}`);
      } else {
        alert(response.statusText);
      }
    } catch (e) {
      alert(e);
    }
  });

  useEffect(() => {
    if (property) {
      reset(propertyToForm(property));
    }
  }, [reset, property]);

  return (
    <ThemeProvider theme={defaultTheme}>
      <CssBaseline />
      <Container>
        <Paper>
          <Typography variant="h3"> {property?.name}</Typography>
          {address && (
            <Typography>
              {address?.address} {address?.city}, {address?.state}{" "}
              {address?.zip}
            </Typography>
          )}
        </Paper>
      </Container>
      <Divider />
      <form onSubmit={onSubmit}>
        <Container>
          <Paper
            style={{ display: "grid", padding: "20px" }}
            sx={{
              "& .MuiTextField-root": { margin: 1 },
            }}
          >
            <FormInputText
              control={control}
              name={"description"}
              label={"Description"}
            />
            <FormInputText
              control={control}
              name={"address"}
              label={"Address"}
            />
            <FormInputText control={control} name={"city"} label={"City"} />
            <FormInputText control={control} name={"state"} label={"State"} />
            <FormInputText control={control} name={"zip"} label={"Zip"} />
            <Button size="small" type={"submit"}>
              Submit
            </Button>
          </Paper>
        </Container>
      </form>
    </ThemeProvider>
  );
}
